import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/swiper-bundle.css";
import lottie from "vue-lottie/src/lottie.vue";
import * as firstAnimationData from "~/assets/lottie/lightmodeSearch.json";
import * as secondAnimationData from "~/assets/lottie/darkmodeSearch.json";

export default {
  name: "swiper-example-multiple-slides-per-biew",
  title: "Multiple slides per view",
  components: {
    Swiper,
    SwiperSlide,
    lottie,
  },
  data() {
    return {
      anim: {
        firstAnim: null,
        secondAnim: null,
      },
      lottieOptions: {
        first: { animationData: firstAnimationData.default },
        second: { animationData: secondAnimationData.default },
      },
      isDark: false,
    };
  },
  beforeCreate() {},
  mounted() {
    setTimeout(() => this.initSetup(), 100);
  },
  methods: {
    initSetup() {
      this.$colorMode.value == "light"
        ? (this.isDark = false)
        : (this.isDark = true);
    },
    toggle() {
      this.$colorMode.preference =
        this.$colorMode.value == "light" ? "dark" : "light";

      if (this.$colorMode.value == "light") {
        this.isDark = true;
      } else {
        this.isDark = false;
      }
      this.$forceUpdate();
    },
    handleAnimation: function (anim, type) {
      this.anim[type] = anim;
    },
    play: function (type) {
      this.anim[type].play();
    },
    stop: function (type) {
      this.anim[type].stop();
    },
  },
};
